<template>
  <v-app class="container-fluid p-0 m-0">
    <v-main>
      <app-loader-screen></app-loader-screen>
      <app-add-comment-modal></app-add-comment-modal>
      <router-view></router-view>
    </v-main>
  </v-app>
</template>

<script>
import loaderScreen from "./components/common/loaderScreen";
import addCommentModal from "./components/modals/addCommentModal";
import io from "socket.io-client";
export default {
  name: "App",
  components: {
    appLoaderScreen: loaderScreen,
    appAddCommentModal: addCommentModal,
  },
  data() {
    return {
      socket: io(this.$http.options.socketUrl),
    };
  },
  created() {
    this.$store.dispatch("initAuth");

    //socket işlemleri ****
    this.socket.on("newConversation", (m) => {
      this.$store.commit('setConversations', m)
    });
  },
};
</script>

